import axios from "axios";

const checkingToken = async (access, refresh) => {
  if (access !== "" && refresh !== "") {
    let acc = access.slice(1, -1);
    const request = await axios
      .post("http://127.0.0.1:8000/auth/jwt/verify/", {
        token: acc,
      })
      .catch(() => false);
    if (request === false) {
      return false;
    }
    return true;
  }
};

const isToken = async () => {
  const isaccess = localStorage.getItem("access");
  const isrefresh = localStorage.getItem("refresh");
  if (isaccess === null || isrefresh === null) {
    localStorage.setItem("isAuth", JSON.stringify(false));
    return false;
  } else {
    const valid = await checkingToken(isaccess, isrefresh);
    if (valid === false) {
      localStorage.setItem("isAuth", JSON.stringify(false));
      localStorage.removeItem("access");
      localStorage.removeItem("refresh");
      return false;
    } else {
      console.log("true");
      localStorage.setItem("isAuth", JSON.stringify(true));
      return true;
    }
  }
};

export { isToken };
