import React, { useEffect } from "react";
import "./style.css";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import BookItem from "../catalog/book-item";
import { getBooks } from "../../service/thunk/books-thunk";

const BookList = ({ books, params, getBooks, light, booksLoading, count }) => {
  useEffect(() => {
    getBooks("?" + params.name);
  }, [params, getBooks]);

  return (
    <div>
      <div className="row book-list-filter">
        {books.map((item, index) => (
          <div className="col-lg-2 col-md-3 col-4 p-0" key={index}>
            <BookItem item={item} margin={0} light={light} />
          </div>
        ))}
      </div>
      {/* <Pagination params={params.name} numPage={count}/> */}
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    books: state.bookReducer.books,
    count: state.bookReducer.count,
    booksLoading: state.bookReducer.booksLoading,
  };
};
export default connect(mapStateToProps, { getBooks })(withRouter(BookList));
