import Profile from "../api/profile";
import { GET_MY_USER, GET_USER } from "../actions/profile-action";

const api = new Profile();

const getMyUser = (access) => {
  return (dispatch) => {
    api.getMyUser(access).then((data) => {
      dispatch(GET_MY_USER(data));
    });
  };
};

const getUserById = (id) => {
  return (dispatch) => {
    api.getUserById(id).then((data) => {
      dispatch(GET_USER(data));
    });
  };
};

export { getMyUser, getUserById };
