import React, { useRef, useEffect } from "react";
import { connect } from "react-redux";
import {
  getCategory,
  getTypes,
  getGenre,
} from "../../service/thunk/books-thunk";
import { withRouter } from "react-router-dom";
const concatFilter = (arr) => {
  let result = "";
  let keys = [];

  arr.forEach((item) => {
    for (let key in item) {
      keys.push(key);
    }
  });
  keys = [...new Set(keys)];
  keys.forEach((key) => {
    let concatvalue = "";
    arr.forEach((item) => {
      if (item[key] !== undefined) {
        concatvalue += `${item[key]},`;
      }
    });
    concatvalue = concatvalue.substring(0, concatvalue.length - 1);
    result += `&${key}=${concatvalue}`;
  });
  return result;
};

const FilterBlock = ({
  light,
  types,
  genre,
  category,
  getCategory,
  getTypes,
  getGenre,
  history,
  params,
}) => {
  const filtersRef = useRef(null);
  const searchRef = useRef(null);

  const getinputValue = (name) => {
    if (name) {
      const index = name.indexOf("&");
      if (index > -1) {
        const res = name.slice(0, index).replace("title=", "");
        return res;
      } else {
        const res = name.replace("title=", "");
        return res;
      }
    }
  };

  let searchValue = getinputValue(params.name);

  useEffect(() => {
    getCategory();
    getTypes();
    getGenre();
  }, [getCategory, getTypes, getGenre]);

  const checkFilter = () => {
    const dropdowns = filtersRef.current;
    const dropdownChoices = dropdowns.querySelectorAll(".filter-choice");
    let filters = [];
    dropdownChoices.forEach((item) => {
      const parentId = item.parentElement.id;
      const children = item.childNodes;
      if (children[0].checked) {
        filters.push({ [parentId]: children[1].textContent });
      }
    });
    const filtered = concatFilter(filters);
    const search = searchRef.current.value;
    const result =
      `title=${search.length > 0 ? search : ""}` + filtered + "&page=1";
    history.push(`/filter/${result}`);
  };

  return (
    <div className="filters w-100">
      <h3 style={{ height: "3rem", margin: "0", textAlign: "center" }}>
        Фильтры
      </h3>
      <div className="filter-block">
        <input
          className={"filter-input" + (light ? " input-white" : " input-black")}
          placeholder="Поиск"
          ref={searchRef}
          defaultValue={searchValue}
        />
        <div className="dropwdowns" ref={filtersRef}>
          <div className="filter-dropdown">
            <button
              className={
                "filter-dropdown-button m-0" +
                (light ? " dropdown-white" : " dropdown-black")
              }
            >
              Типы
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                className="bi bi-caret-down dropdown-icon"
                viewBox="0 0 16 16"
              >
                <path d="M3.204 5h9.592L8 10.481 3.204 5zm-.753.659l4.796 5.48a1 1 0 0 0 1.506 0l4.796-5.48c.566-.647.106-1.659-.753-1.659H3.204a1 1 0 0 0-.753 1.659z" />
              </svg>
            </button>
            <div
              className={
                "dropdown-list first" +
                (light ? " dropdown-white" : " dropdown-black")
              }
              id="types"
            >
              {types.map((item, index) => (
                <div className="filter-choice" key={index}>
                  <input type="checkbox" className="filter-choice-checkbox" />
                  <label>{item.title}</label>
                </div>
              ))}
            </div>
          </div>
          <div className="filter-dropdown">
            <button
              className={
                "filter-dropdown-button m-0" +
                (light ? " dropdown-white" : " dropdown-black")
              }
            >
              Жанры
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                className="bi bi-caret-down dropdown-icon"
                viewBox="0 0 16 16"
              >
                <path d="M3.204 5h9.592L8 10.481 3.204 5zm-.753.659l4.796 5.48a1 1 0 0 0 1.506 0l4.796-5.48c.566-.647.106-1.659-.753-1.659H3.204a1 1 0 0 0-.753 1.659z" />
              </svg>
            </button>
            <div
              className={
                "dropdown-list first" +
                (light ? " dropdown-white" : " dropdown-black")
              }
              id="genres"
            >
              {genre.map((item, index) => (
                <div className="filter-choice" key={index}>
                  <input type="checkbox" className="filter-choice-checkbox" />
                  <label>{item.title}</label>
                </div>
              ))}
            </div>
          </div>
          <div className="filter-dropdown">
            <button
              className={
                "filter-dropdown-button m-0" +
                (light ? " dropdown-white" : " dropdown-black")
              }
            >
              Категории
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                className="bi bi-caret-down dropdown-icon"
                viewBox="0 0 16 16"
              >
                <path d="M3.204 5h9.592L8 10.481 3.204 5zm-.753.659l4.796 5.48a1 1 0 0 0 1.506 0l4.796-5.48c.566-.647.106-1.659-.753-1.659H3.204a1 1 0 0 0-.753 1.659z" />
              </svg>
            </button>
            <div
              className={
                "dropdown-list" +
                (light ? " dropdown-white" : " dropdown-black")
              }
              id="category"
            >
              {category.map((item, index) => (
                <div className="filter-choice" key={index}>
                  <input type="checkbox" className="filter-choice-checkbox" />
                  <label>{item.title}</label>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
        }}
      >
        <button
          onClick={() => checkFilter()}
          className={"btn mt-3" + (light ? " btn-secondary" : " btn-dark")}
          style={{
            paddingLeft: "2rem",
            paddingRight: "2rem",
            fontSize: "18px",
          }}
        >
          Найти
        </button>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    light: state.bookReducer.light,
    types: state.bookReducer.types,
    genre: state.bookReducer.genre,
    category: state.bookReducer.category,
  };
};

export default connect(mapStateToProps, {
  getCategory,
  getTypes,
  getGenre,
})(withRouter(FilterBlock));
