const initialState = {
  isAuth: false,
  authError: false,
  myuser: [],
  user: [],
};

const ProfileReducer = (state = initialState, action) => {
  switch (action.type) {
    case "SET_AUTH":
      return { ...state, isAuth: true };
    case "DEL_AUTH":
      return { ...state, isAuth: false };
    case "AUTH_ERROR":
      return { ...state, authError: true };
    case "GET_MY_USER":
      return { ...state, myuser: action.data };
    case "GET_USER_BY_ID":
      return { ...state, user: action.data };
    default:
      return state;
  }
};

export default ProfileReducer;
