import axios from "axios";

function getCookie(name) {
  let cookieValue = null;
  if (document.cookie && document.cookie !== "") {
    const cookies = document.cookie.split(";");
    for (let i = 0; i < cookies.length; i++) {
      const cookie = cookies[i].trim();
      // Does this cookie string begin with the name we want?
      if (cookie.substring(0, name.length + 1) === name + "=") {
        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
        break;
      }
    }
  }
  return cookieValue;
}

class BooksApi {
  async getMostRatedBooks() {
    const res = await axios.get(
      "http://127.0.0.1:8000/api/books/filtered?average_rating=1"
    );
    if (res.status === 200) {
      return res.data;
    } else {
      return 404;
    }
  }
  async getMostPopularBooks() {
    const res = await axios.get(
      "http://127.0.0.1:8000/api/books/filtered?views=1"
    );

    if (res.status === 200) {
      return res.data;
    } else {
      return 404;
    }
  }
  async getTheNewestChapters() {
    const res = await axios.get("http://127.0.0.1:8000/api/books/chapters");

    if (res.status === 200) {
      return res.data;
    } else {
      return 404;
    }
  }
  async getFilterList(name) {
    const res = await axios.get(`http://127.0.0.1:8000/api/books/${name}`);

    if (res.status === 200) {
      return res.data;
    } else {
      return 404;
    }
  }
  async getAllOrFilterBooks(filter) {
    const res = await axios.get(`http://127.0.0.1:8000/api/books/${filter}`);

    if (res.status === 200) {
      return res.data;
    } else {
      return 404;
    }
  }
  async getChapterByBook(id, page = 1) {
    const res = await axios.get(
      `http://127.0.0.1:8000/api/books/book-detail-c/${id}?page=${page}`
    );
    if (res.status === 200) {
      return res.data;
    } else {
      return 404;
    }
  }
  async getDetailBook(id, access) {
    let token = null;
    if (access) {
      token = access.slice(1, -1);
    }
    const idx = id.replace("id=", "");
    let res;
    if (token !== null) {
      res = await axios.get(`http://127.0.0.1:8000/api/books/${idx}`, {
        headers: {
          Authorization: "JWT " + access,
        },
      });
    } else {
      res = await axios.get(`http://127.0.0.1:8000/api/books/${idx}`);
    }
    console.log(res);
    if (res.status === 200) {
      return res.data;
    } else {
      return 404;
    }
  }
  async getPagesByChapter(id) {
    const res = await axios.get(
      `http://127.0.0.1:8000/api/books/chapter/${id}`
    );
    if (res.status === 200) {
      return res.data;
    } else {
      return 404;
    }
  }
  async setLike(book_id, access) {
    const res = await axios.put(
      `http://127.0.0.1:8000/api/books/${book_id}`,
      {
        likes_update: 1,
      },
      {
        headers: {
          Authorization: "JWT " + access,
        },
      },
      {
        "X-CSRFToken": getCookie("csrftoken"),
      }
    );
    if (res.status === 200) {
      return res.data;
    } else {
      throw new Error("bad request");
    }
  }
  async getReviews(id) {
    const res = await axios.get(
      `http://127.0.0.1:8000/api/books/book-review/${id}`
    );
    if (res.status === 200) {
      return res.data;
    } else {
      return 404;
    }
  }
  async setReview(book_id, user_id, star, text, access) {
    const res = await axios.post(
      `http://127.0.0.1:8000/api/books/set_rating`,
      {
        user: user_id,
        book: book_id,
        star: star,
        text: text,
      },
      {
        headers: {
          Authorization: "JWT " + access,
        },
      },
      {
        "X-CSRFToken": getCookie("csrftoken"),
      }
    );
    if (res.status === 201) {
      return res.data;
    } else {
      throw new Error("bad request");
    }
  }
}

export default BooksApi;
