import React from "react";
import { withRouter } from "react-router-dom";
import "./style.css";

const PopularItem = ({ item, history }) => {
  return (
    <div
      className="pop-compos"
      onClick={() => history.push(`/detail/id=${item.id}`)}
    >
      <div className="pop-img-wrapper">
        <img src={item.poster} className="pop-img" alt="poster" />
      </div>
      <div className="pop-info">
        <h6>{item.title}</h6>
      </div>
    </div>
  );
};

export default withRouter(PopularItem);
