import React from "react";
import { withRouter } from "react-router-dom";
import "./style.css";

const ComposItem = ({ item, light, history }) => {
  return (
    <div
      className="new-compos"
      style={{ background: light ? "#fafafa" : "#191919" }}
      onClick={() => history.push(`/detail/id=${item.book.id}`)}
    >
      <div className="compos-img-wrapper">
        <img src={item.book.poster} className="comspos-img" alt="poster" />
      </div>
      <div className="compos-info">
        <h6>{item.book.title}</h6>
        <h6>{item.title} </h6>
        <span>{item.created_date}</span>
      </div>
    </div>
  );
};

const ComposItemMobile = ({ item, light, history }) => {
  return (
    <div
      className="new-compos"
      style={{ background: light ? "#fafafa" : "#191919" }}
      onClick={() => history.push(`/detail/id=${item.book.id}`)}
    >
      <div className="compos-info">
        <div className="limit-line">
          <h6 className="compos-title">{item.book.title}</h6>
        </div>
        <div className="limit-line">
          <span>{item.title} </span>
        </div>
        <span>{item.created_date}</span>
      </div>
    </div>
  );
};

const ComposItemMobileWR = withRouter(ComposItemMobile);
const ComposItemWR = withRouter(ComposItem);

export { ComposItemMobileWR, ComposItemWR };
