const initialState = {
  mostRated: [],
  mostPopular: [],
  newest: [],
  booksLoading: false,
  light: false,
  types: [],
  genre: [],
  category: [],
  books: [],
  count: 0,
  next: "",
  previous: "",
};

const bookReducer = (state = initialState, action) => {
  switch (action.type) {
    case "FETCHING_DATA":
      return { ...state, booksLoading: true };
    case "GET_THE_MOST_RATED":
      return { ...state, mostRated: action.data, booksLoading: false };
    case "GET_THE_MOST_POPULAR":
      return { ...state, mostPopular: action.data, booksLoading: false };
    case "GET_THE_NEWEST":
      return { ...state, newest: action.data, booksLoading: false };
    case "GET_TYPES":
      return { ...state, types: action.data };
    case "GET_GENRE":
      return { ...state, genre: action.data };
    case "GET_CATEGORY":
      return { ...state, category: action.data };
    case "GET_BOOKS":
      return {
        ...state,
        books: action.data.results,
        count: action.data.page_count,
        next: action.data.next,
        previous: action.data.previous,
        booksLoading: false,
      };
    case "CHANGE_THEME":
      return { ...state, light: !state.light };
    default:
      return state;
  }
};

export default bookReducer;
