import React from "react";
import { connect } from "react-redux";

let CustomField = ({
  input,
  type,
  placeholder,
  label,
  meta: { touched, error, warning },
  light,
}) => {
  return (
    <div style={{ marginBottom: "15px" }}>
      <label className="label">{label}:</label>
      <input
        {...input}
        placeholder={placeholder}
        type={type}
        className={"form-control " + (light ? "light-input" : "black-input")}
      />
      {touched &&
        ((error && <span className="text-danger">{error}</span>) ||
          (warning && <span className="text-danger">{warning}</span>))}
    </div>
  );
};

let GenderInput = ({
  name,
  label,
  meta: { touched, error, warning },
  light,
}) => {
  return (
    <div style={{ marginBottom: "15px" }}>
      <label className="label">{label}:</label>
      <select
        name={name}
        className={"form-control " + (light ? "light-input" : "black-input")}
        style={light ? { color: "aqua" } : { color: "red" }}
      >
        <option></option>
        <option value="Male">Мужской</option>
        <option value="Female">Женский</option>
      </select>
      {touched &&
        ((error && <span className="text-danger">{error}</span>) ||
          (warning && <span className="text-danger">{warning}</span>))}
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    light: state.bookReducer.light,
  };
};

CustomField = connect(mapStateToProps)(CustomField);

GenderInput = connect(mapStateToProps)(GenderInput);

export { CustomField, GenderInput };
