import React from "react";
import { Field, reduxForm } from "redux-form";
import { Link, withRouter } from "react-router-dom";
import {
  SET_AUTH,
  DEL_AUTH,
  AUTH_ERROR,
} from "../../service/actions/profile-action";
import { connect } from "react-redux";
import { required } from "./validator";
import "./style.css";
import { CustomField } from "./input";
import Profile from "../../service/api/profile";

let LoginForm = (props) => {
  const Login = (values) => {
    const profileApi = new Profile();
    profileApi
      .loginUser(values.username, values.password)
      .then((tokens) => {
        props.SET_AUTH();
        localStorage.setItem("access", JSON.stringify(tokens.access));
        localStorage.setItem("refresh", JSON.stringify(tokens.refresh));
        props.history.push("/");
        window.location.reload(false);
      })
      .catch(() => {
        props.AUTH_ERROR();
      });
  };
  return (
    <div className={"wrapper "}>
      <div
        className={"form-wrapper " + (props.light ? "light-bg" : "black-bg")}
      >
        <div className="custom-container">
          <div className="form-block">
            <form
              className={
                "login-form " + (props.light ? "light-form" : "black-form")
              }
              onSubmit={props.handleSubmit(Login)}
            >
              <div className="form-title">
                <h5>LOGIN</h5>
              </div>
              {props.authError ? (
                <span className="text-danger">
                  Неправильный логин или пароль
                </span>
              ) : null}
              <Field
                name="username"
                component={CustomField}
                type="text"
                placeholder="Username"
                className="form-control"
                props={{ label: "Введите ваш логин" }}
                validate={[required]}
              />
              <Field
                name="password"
                component={CustomField}
                type="password"
                placeholder="Password"
                className="form-control"
                props={{ label: "Введите ваш пароль" }}
              />
              <button
                className={"btn btn-warning form-btn"}
                style={{ marginBottom: "20px" }}
                type="submit"
              >
                Войти
              </button>
              <span>
                Нет аккаунта? <Link to="/register/">Sign Up</Link>
              </span>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};
const mapStateToProps = (state) => {
  return {
    light: state.bookReducer.light,
    isAuth: state.ProfileReducer.isAuth,
    authError: state.ProfileReducer.authError,
  };
};

LoginForm = reduxForm({
  form: "login-from",
})(LoginForm);

export default connect(mapStateToProps, { SET_AUTH, DEL_AUTH, AUTH_ERROR })(
  withRouter(LoginForm)
);
