import { createStore, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import { reducer as formReducer } from "redux-form";
import { combineReducers } from "redux";

import bookReducer from "./components/reducers/books";
import BookDetailReducer from "./components/reducers/detail-book";
import ProfileReducer from "./components/reducers/profile";

const store = createStore(
  combineReducers({
    bookReducer,
    BookDetailReducer,
    ProfileReducer,
    form: formReducer,
  }),
  applyMiddleware(thunk)
);

export default store;
