import React, { useEffect } from "react";
import "./style.css";
import { ComposItemWR, ComposItemMobileWR } from "./compostion-item";
import PopularItem from "./popular";
import {
  getMostPopularBooks,
  getTheNewestChapters,
} from "../../service/thunk/books-thunk";
import { connect } from "react-redux";

const Catalog2 = ({
  mostPopular,
  newest,
  light,
  getMostPopularBooks,
  getTheNewestChapters,
}) => {
  useEffect(() => {
    getMostPopularBooks();
    getTheNewestChapters();
    console.log("main-block");
  }, [getMostPopularBooks, getTheNewestChapters]);

  return (
    <div className={"catalog-2 " + (light ? "light-bg" : "black-bg")}>
      <div className="line"></div>
      <div className="catalog-2-main-block">
        <div className="row">
          <div className="new-compositions col-12 col-lg-8">
            <h3 style={{ paddingBottom: "2rem" }}>Обновления</h3>
            <div className="">
              {window.screen.width > 900 ? (
                newest.map((item, index) => (
                  <ComposItemWR item={item} key={index} light={light} />
                ))
              ) : (
                <div className="new-compos-mob">
                  {newest.map((item, index) => (
                    <ComposItemMobileWR item={item} key={index} light={light} />
                  ))}
                </div>
              )}
            </div>
          </div>
          <div className="popular-compos col-12 col-lg-4">
            <h3 style={{ paddingBottom: "2rem" }}>Популярное</h3>
            <div>
              {mostPopular.map((item, index) => (
                <PopularItem item={item} key={index} />
              ))}
            </div>
          </div>
          <div className="bottom-line"></div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    mostPopular: state.bookReducer.mostPopular,
    newest: state.bookReducer.newest,
    light: state.bookReducer.light,
  };
};

export default connect(mapStateToProps, {
  getMostPopularBooks,
  getTheNewestChapters,
})(Catalog2);
