import React, { useEffect } from "react";
import MainPage from "./components/mainpage/mainpage";
import { Route, Link } from "react-router-dom";
import FilterMainPage from "./components/filter-page/main-block";
import { connect } from "react-redux";
import { CHANGE_THEME } from "./service/actions/books-action";
import { SET_AUTH, DEL_AUTH } from "./service/actions/profile-action";
import { getMyUser } from "./service/thunk/profile";
import DetailPage from "./components/detail-page/detail";
import PagesBlock from "./components/detail-page/page";
import Loginform from "./components/auth/login";
import RegisterForm from "./components/auth/register";
import { PrivateProfile } from "./components/auth/profile";
import Public from "./components/auth/public";
import { isToken } from "./service/auth/JWT";

const App = ({ CHANGE_THEME, SET_AUTH, DEL_AUTH, getMyUser }) => {
  useEffect(() => {
    const status = isToken();
    status.then((value) => {
      if (value) {
        SET_AUTH();
        const access = localStorage.getItem("access").slice(1, -1);
        getMyUser(access);
      } else {
        DEL_AUTH();
      }
    });
  }, [getMyUser, SET_AUTH, DEL_AUTH]);
  return (
    <div style={{ position: "relative" }}>
      <nav className="my-nav">
        <div className="main-nav-part">
          <div>
            <Link to="/" style={{ textDecoration: "none" }}>
              <h3 className="title-brand">TITLE</h3>
            </Link>
          </div>
          {window.screen.width > 800 ? (
            <Link to="/filter/" style={{ textDecoration: "none" }}>
              <div className="cat-link">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  fill="currentColor"
                  className="bi bi-bar-chart-line-fill cat-icon"
                  viewBox="0 0 16 16"
                >
                  <path d="M11 2a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1v12h.5a.5.5 0 0 1 0 1H.5a.5.5 0 0 1 0-1H1v-3a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1v3h1V7a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1v7h1V2z" />
                </svg>
                <span>Каталог</span>
              </div>
            </Link>
          ) : null}
        </div>
        <div className="right-side-nav">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            fill="currentColor"
            className="bi bi-heptagon-half colorChanger"
            viewBox="0 0 16 16"
            onClick={() => {
              CHANGE_THEME();
            }}
          >
            <path d="M7.779.052a.5.5 0 0 1 .442 0l6.015 2.97a.5.5 0 0 1 .267.34l1.485 6.676a.5.5 0 0 1-.093.415l-4.162 5.354a.5.5 0 0 1-.395.193H4.662a.5.5 0 0 1-.395-.193L.105 10.453a.5.5 0 0 1-.093-.415l1.485-6.676a.5.5 0 0 1 .267-.34L7.779.053zM8 15h3.093l3.868-4.975-1.383-6.212L8 1.058V15z" />
          </svg>
          <Link to="/me/">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="16"
              height="16"
              fill="currentColor"
              className="bi bi-person-lines-fill profileIcon"
              viewBox="0 0 16 16"
            >
              <path d="M6 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm-5 6s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zM11 3.5a.5.5 0 0 1 .5-.5h4a.5.5 0 0 1 0 1h-4a.5.5 0 0 1-.5-.5zm.5 2.5a.5.5 0 0 0 0 1h4a.5.5 0 0 0 0-1h-4zm2 3a.5.5 0 0 0 0 1h2a.5.5 0 0 0 0-1h-2zm0 3a.5.5 0 0 0 0 1h2a.5.5 0 0 0 0-1h-2z" />
            </svg>
          </Link>
        </div>
      </nav>
      <Route path="/" component={MainPage} exact />
      <Route
        path="/filter/:name?"
        render={({ match }) => {
          return <FilterMainPage params={match.params} />;
        }}
        exact
      />
      <Route
        path="/detail/:id"
        render={({ match }) => {
          return <DetailPage params={match.params} />;
        }}
        exact
      />
      <Route
        path="/detail/pages/:id"
        render={({ match }) => {
          return <PagesBlock params={match.params} />;
        }}
        exact
      />
      <Route path="/login/" component={Loginform} exact />
      <Route path="/register/" component={RegisterForm} exact />
      <Route path="/me/" component={PrivateProfile} exact />
      <Route
        path="/profile/:id?"
        render={({ match }) => <Public params={match.params.id} />}
        exact
      />
    </div>
  );
};

export default connect(null, { CHANGE_THEME, SET_AUTH, DEL_AUTH, getMyUser })(
  App
);
