import React from "react";
import { withRouter } from "react-router-dom";

const BookItem = ({ item, margin, light, history }) => {
  return (
    <div
      className={"book-card" + (margin === 0 ? " book-card-filter" : "")}
      onClick={() => {
        history.push(`/detail/id=${item.id}`);
      }}
    >
      <img src={item.poster} className="book-image" alt="poster" />
      <h6
        className="book-name"
        style={light ? { color: "black" } : { color: "white" }}
      >
        {item.title}
      </h6>
      <span
        className="book-list-cat"
        style={light ? { color: "black" } : { color: "white" }}
      >
        {item.types}
      </span>
    </div>
  );
};

export default withRouter(BookItem);
