import React, { useState } from "react";
import { Redirect, withRouter } from "react-router-dom";
import BooksApi from "../../service/api/books-api";

const RatingPopUp = ({
  isOpen,
  setOpen,
  isAuth,
  book_id,
  user_id,
  history,
}) => {
  const [chosenStar, setStar] = useState(0);
  const [text, setText] = useState("");

  const setComment = (text) => {
    let finishedText = text;
    if (text.match(/(\r\n|\n|\r)/gm) !== null) {
      finishedText = text.replace(/(\r\n|\n|\r)/gm, "<br>");
    }
    if (finishedText.match(/\s{2,}/g) !== null) {
      finishedText = finishedText.replace(/\s{2,}/g, " ");
    }
    setText(finishedText);
  };

  const publish = () => {
    let access = localStorage.getItem("access");
    if (access) {
      access = access.slice(1, -1);
      const api = new BooksApi();
      api.setReview(book_id, user_id, chosenStar, text, access);
      setOpen(false);
    } else {
      localStorage.setItem("isAuth", "false");
      localStorage.removeItem("access");
      localStorage.removeItem("refresh");
      window.location.reload(false);
      history.push("/login/");
    }
  };

  if (isOpen) {
    if (isAuth || JSON.parse(localStorage.getItem("isAuth"))) {
      return (
        <React.Fragment>
          <svg style={{ display: "none" }}>
            <symbol id="star2" viewBox="0 -10 511.98685 511">
              <g>
                <path d="m510.652344 185.902344c-3.351563-10.367188-12.546875-17.730469-23.425782-18.710938l-147.773437-13.417968-58.433594-136.769532c-4.308593-10.023437-14.121093-16.511718-25.023437-16.511718s-20.714844 6.488281-25.023438 16.535156l-58.433594 136.746094-147.796874 13.417968c-10.859376 1.003906-20.03125 8.34375-23.402344 18.710938-3.371094 10.367187-.257813 21.738281 7.957031 28.90625l111.699219 97.960937-32.9375 145.089844c-2.410156 10.667969 1.730468 21.695313 10.582031 28.09375 4.757813 3.4375 10.324219 5.1875 15.9375 5.1875 4.839844 0 9.640625-1.304687 13.949219-3.882813l127.46875-76.183593 127.421875 76.183593c9.324219 5.609376 21.078125 5.097657 29.910156-1.304687 8.855469-6.417969 12.992187-17.449219 10.582031-28.09375l-32.9375-145.089844 111.699219-97.941406c8.214844-7.1875 11.351563-18.539063 7.980469-28.925781zm0 0" />
              </g>
            </symbol>
          </svg>
          <div className="popup-background">
            <div className="popup-form">
              <span className="close" onClick={() => setOpen(false)}>
                X
              </span>
              <div className="set-star">
                {[1, 2, 3, 4, 5].map((item) => (
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                    }}
                    key={item}
                  >
                    <svg
                      className={
                        "icon-detail star" +
                        (chosenStar === item ? " chosenStar" : "")
                      }
                      onClick={() => setStar(item)}
                    >
                      <use xlinkHref="#star2"></use>
                    </svg>
                    <span
                      className="star-value"
                      style={{ color: "white", paddingRight: "10px" }}
                    >
                      {item}
                    </span>
                  </div>
                ))}
              </div>
              <div className="set-comment-value">
                <div
                  style={{
                    color: "white",
                    marginBottom: "10px",
                    marginTop: "10px",
                  }}
                >
                  <h6>Оставьте свой комментарий:</h6>
                </div>
                <textarea
                  rows="5"
                  cols="50"
                  style={{
                    background: "black",
                    outline: "none",
                    border: "0",
                    color: "white",
                  }}
                  className="form-control"
                  onChange={(e) => setComment(e.target.value)}
                ></textarea>
                <button
                  className="btn btn-warning mt-2"
                  onClick={() => publish()}
                >
                  Опубликовать
                </button>
              </div>
            </div>
          </div>
        </React.Fragment>
      );
    } else {
      return <Redirect to="/login/" />;
    }
  } else {
    return null;
  }
};

export default withRouter(RatingPopUp);
