import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { required } from "./validator";
import { CustomField } from "./input";
import { Redirect, withRouter } from "react-router-dom";
import { SET_AUTH, DEL_AUTH } from "../../service/actions/profile-action";
import { getMyUser } from "../../service/thunk/profile";
import Profile from "../../service/api/profile";
import ImagePopUp from "./image-popup";
import BookItem from "../catalog/book-item";

import "./style.css";

let Private = (props) => {
  useEffect(() => {
    if (localStorage.getItem("access")) {
      const access = localStorage.getItem("access").slice(1, -1);
      props.getMyUser(access);
    }
  }, [props]);

  useEffect(() => {
    props.initialize(
      {
        name: props.myuser.first_name,
        username: props.myuser.username,
        gender: props.myuser.gender,
      },
      [props.myuser]
    );
  });

  const changeValues = (values) => {
    const api = new Profile();
    const access = localStorage.getItem("access").slice(1, -1);
    api.changeMyProfile(values.username, values.name, values.gender, access);
  };

  const [subUrl, setSub] = useState(1);
  const [open, setOpen] = useState(false);

  if (props.isAuth || JSON.parse(localStorage.getItem("isAuth"))) {
    return (
      <div className="wrapper">
        <div
          className={
            "profile-wrapper " + (props.light ? "light-bg" : "black-bg")
          }
        >
          <div className="custom-container">
            <div
              className={
                "detail-block row " +
                (props.light ? "detail-block-lt" : "detail-block-bl")
              }
            >
              <div className="detail-img-block col-12 col-sm-3">
                <div
                  style={{
                    display: "flex",
                    width: "100%",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                >
                  <img
                    src={props.myuser.image}
                    className="detail-img profile-img"
                    alt="profile-img"
                  />
                </div>
                <div className="read">
                  <button
                    className="btn btn-success btn-block"
                    onClick={() => setOpen(!open)}
                  >
                    Изменить фото
                  </button>
                </div>
              </div>
              <div
                className="detail-info-block col-12 col-sm-9"
                style={props.light ? { color: "black" } : { color: "white" }}
              >
                <div className="detail-main">
                  <div className="detail-nav prof-nav">
                    <span
                      className={
                        "nav-btn " + (subUrl === 1 ? "active-btn" : "")
                      }
                      onClick={() => {
                        setSub(1);
                      }}
                    >
                      Информация
                    </span>
                    <span
                      className={
                        "nav-btn " + (subUrl === 2 ? "active-btn" : "")
                      }
                      onClick={() => {
                        setSub(2);
                      }}
                    >
                      Понравившиеся
                    </span>
                  </div>
                  {subUrl === 1 ? (
                    <form
                      style={{ marginLeft: "10px" }}
                      onSubmit={props.handleSubmit(changeValues)}
                    >
                      <Field
                        name="name"
                        component={CustomField}
                        type="text"
                        placeholder="Name"
                        className="form-control"
                        props={{ label: "Ваше Имя" }}
                        validate={[required]}
                      />
                      <Field
                        name="username"
                        component={CustomField}
                        type="text"
                        placeholder="Username"
                        className="form-control"
                        props={{ label: "Ваш логин" }}
                        validate={[required]}
                      />
                      <div style={{ marginBottom: "15px" }}>
                        <label className="label">Выберите пол:</label>
                        <Field
                          name="gender"
                          type="text"
                          component="select"
                          props={{ label: "Выберите пол" }}
                          className={
                            "form-control " +
                            (props.light ? "light-input" : "black-input")
                          }
                          style={
                            props.light ? { color: "aqua" } : { color: "red" }
                          }
                        >
                          <option value="male">Мужской</option>
                          <option value="female">Женский</option>
                        </Field>
                      </div>
                      <button className="btn btn-warning btn-block">
                        Сохранить изменения
                      </button>
                      <button
                        className="btn btn-danger btn-block"
                        style={{ marginTop: "15px" }}
                        onClick={() => {
                          localStorage.removeItem("access");
                          localStorage.removeItem("refresh");
                          props.DEL_AUTH();
                          window.location.reload(false);
                          props.history.push("/");
                        }}
                      >
                        Выйти
                      </button>
                    </form>
                  ) : (
                    <div className="row">
                      {props.myuser.books_like.slice(0, 6).map((item) => (
                        <div
                          className="col-xl-4 col-md-4 col-6"
                          style={{ marginTop: "15px" }}
                        >
                          <BookItem item={item} />
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        <ImagePopUp isOpen={open} setOpen={setOpen} />
      </div>
    );
  } else {
    return <Redirect to="/login/" />;
  }
};

Private = reduxForm({
  form: "profile",
})(Private);

const privateStateToProps = (state) => {
  return {
    light: state.bookReducer.light,
    isAuth: state.ProfileReducer.isAuth,
    myuser: state.ProfileReducer.myuser,
  };
};

const PrivateProfile = connect(privateStateToProps, {
  SET_AUTH,
  DEL_AUTH,
  getMyUser,
})(withRouter(Private));

export { PrivateProfile };
