import React from "react";
import ReactPaginate from "react-paginate";
import { withRouter } from "react-router-dom";

const PreviousLabel = ({ light }) => {
  return (
    <span
      className={
        "pagination-btn" + (light ? " light-pagination" : " dark-pagination")
      }
    >
      ‹
    </span>
  );
};
const NextLabel = ({ light }) => {
  return (
    <span
      className={
        "pagination-btn" + (light ? " light-pagination" : " dark-pagination")
      }
    >
      ›
    </span>
  );
};

const Pagination = ({ numPage, light, params, history }) => {
  const onPageChange = (current) => {
    const currentPage = current.selected + 1;
    let match = params.match(/&page=(\d{0,})/gm);
    if (match) {
      let newParams = params.replace(match[0], `&page=${currentPage}`);
      history.push(`/detail/${newParams}`);
    } else {
      history.push(`/detail/${params}&page=${currentPage}`);
    }
  };

  return (
    <ReactPaginate
      pageCount={numPage}
      pageRangeDisplayed={4}
      marginPagesDisplayed={1}
      containerClassName="pagination"
      pageClassName={
        "pagination-btn" + (light ? " light-pagination" : " dark-pagination")
      }
      breakClassName={
        "pagination-btn" + (light ? " light-pagination" : " dark-pagination")
      }
      activeClassName={light ? " light-active" : " dark-active"}
      activeLinkClassName={"active-link"}
      previousLabel={<PreviousLabel light={light} />}
      nextLabel={<NextLabel light={light} />}
      previousClassName="previous-btn"
      nextClassName="next-btn"
      onPageChange={onPageChange}
      initialPage={0}
      forcePage={0}
    />
  );
};

export default withRouter(Pagination);
