import axios from "axios";
function getCookie(name) {
  let cookieValue = null;
  if (document.cookie && document.cookie !== "") {
    const cookies = document.cookie.split(";");
    for (let i = 0; i < cookies.length; i++) {
      const cookie = cookies[i].trim();
      // Does this cookie string begin with the name we want?
      if (cookie.substring(0, name.length + 1) === name + "=") {
        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
        break;
      }
    }
  }
  return cookieValue;
}

class Profile {
  async createUser(username, password, re_password, gender, first_name) {
    const res = await axios.post(
      "http://127.0.0.1:8000/auth/users/",
      {
        username,
        password,
        re_password,
        gender,
        first_name,
      },
      {
        "X-CSRFToken": getCookie("csrftoken"),
      }
    );
    if (res.status === 201) {
      return res.data;
    } else {
      throw new Error("bad request");
    }
  }
  async loginUser(username, password) {
    const res = await axios.post(
      "http://127.0.0.1:8000/auth/jwt/create/",
      {
        username,
        password,
      },
      {
        "X-CSRFToken": getCookie("csrftoken"),
      }
    );
    if (res.status === 200) {
      return res.data;
    } else {
      throw new Error("bad request");
    }
  }
  async getMyUser(access) {
    const res = await axios.get("http://127.0.0.1:8000/api/user/my_profile", {
      "X-CSRFToken": getCookie("csrftoken"),
      headers: {
        Authorization: "JWT " + access,
      },
    });
    if (res.status === 200) {
      return res.data;
    } else {
      throw new Error("bad request");
    }
  }
  async changeMyProfile(username, first_name, gender, access) {
    const res = await axios.put(
      "http://127.0.0.1:8000/api/user/my_profile",
      { username, first_name, gender },
      {
        "X-CSRFToken": getCookie("csrftoken"),
        headers: {
          Authorization: "JWT " + access,
        },
      }
    );
    if (res.status === 200) {
      return res.data;
    } else {
      throw new Error("bad request");
    }
  }
  async setimage(image, access) {
    const res = await axios.post(
      "http://127.0.0.1:8000/api/user/my_profile",
      image,
      {
        headers: {
          Authorization: "JWT " + access,
          "content-type": "multipart/form-data",
        },
      }
    );
    if (res.status === 200) {
      return res.data;
    } else {
      throw new Error("bad request");
    }
  }
  async getUserById(id) {
    const res = await axios.get(`http://127.0.0.1:8000/api/user/${id}`);
    if (res.status === 200) {
      return res.data;
    } else {
      throw new Error("bad request");
    }
  }
}

export default Profile;
