import React from "react";
import "./style.css";
import { connect } from "react-redux";
import FilterBlock from "./filter-block";
import BookList from "./books-list";
import { getBooks } from "../../service/thunk/books-thunk";
import Pagination from "./paginations";

const FilterMainPage = ({ light, params, count }) => {
  return (
    <div className="wrapper">
      <div className={"filter-wrapper" + (light ? " white-bg" : " black-bg")}>
        <div className="custom-container">
          <FilterBlock params={params} />
          <BookList params={params} light={light} />
          <Pagination
            params={params.name}
            numPage={count}
            p={0}
            light={light}
          />
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    light: state.bookReducer.light,
    count: state.bookReducer.count,
  };
};

export default connect(mapStateToProps, { getBooks })(FilterMainPage);
