import React from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import "./style.css";
import { CustomField } from "./input";
import { required, passwordsMatch } from "./validator";
import Profile from "../../service/api/profile";

let RegisterForm = (props) => {
  const Register = (values) => {
    const profileApi = new Profile();
    const gender = values.gender === "empty" ? null : values.gender;
    profileApi
      .createUser(
        values.username,
        values.password,
        values.re_password,
        gender,
        values.name
      )
      .then((tokens) => {
        localStorage.setItem("access", JSON.stringify(tokens.access));
        localStorage.setItem("refresh", JSON.stringify(tokens.refresh));
      });
    props.history.push("/");
  };

  return (
    <div className={"wrapper "}>
      <div
        className={"form-wrapper " + (props.light ? "light-bg" : "black-bg")}
      >
        <div className="custom-container">
          <div className="form-block">
            <form
              className={
                "login-form " + (props.light ? "light-form" : "black-form")
              }
              onSubmit={props.handleSubmit(Register)}
            >
              <div className="form-title">
                <h5>Register</h5>
              </div>
              <Field
                name="name"
                component={CustomField}
                type="text"
                placeholder="Имя"
                className="form-control"
                props={{ label: "Введите ваш имя" }}
                validate={[required]}
              />
              <Field
                name="username"
                component={CustomField}
                type="text"
                placeholder="Username"
                className="form-control"
                props={{ label: "Введите ваш никнейм" }}
                validate={[required]}
              />
              <div style={{ marginBottom: "15px" }}>
                <label className="label">Выберите пол:</label>
                <Field
                  name="gender"
                  type="text"
                  component="select"
                  props={{ label: "Выберите пол" }}
                  className={
                    "form-control " +
                    (props.light ? "light-input" : "black-input")
                  }
                  style={props.light ? { color: "aqua" } : { color: "red" }}
                >
                  <option value="empty"></option>
                  <option value="male">Мужской</option>
                  <option value="female">Женский</option>
                </Field>
              </div>
              <Field
                name="password"
                component={CustomField}
                type="password"
                placeholder="Password"
                className="form-control"
                props={{ label: "Введите ваш пароль" }}
                validate={[required]}
              />
              <Field
                name="re_password"
                component={CustomField}
                type="password"
                placeholder="Password"
                className="form-control"
                props={{ label: "Повторите ваш пароль" }}
                validate={[required, passwordsMatch]}
              />

              <button className={"btn btn-warning form-btn"} type="submit">
                Зарегистрироваться
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    light: state.bookReducer.light,
  };
};

RegisterForm = reduxForm({
  form: "register-from",
})(RegisterForm);

export default connect(mapStateToProps)(RegisterForm);
