const SET_AUTH = () => {
  return {
    type: "SET_AUTH",
  };
};

const DEL_AUTH = () => {
  localStorage.setItem("isAuth", "false");
  return { type: "DEL_AUTh" };
};

const AUTH_ERROR = () => {
  return {
    type: "AUTH_ERROR",
  };
};

const GET_MY_USER = (data) => {
  return {
    type: "GET_MY_USER",
    data,
  };
};

const GET_USER = (data) => {
  return {
    type: "GET_USER_BY_ID",
    data,
  };
};
export { SET_AUTH, DEL_AUTH, AUTH_ERROR, GET_MY_USER, GET_USER };
