import React from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";

const ChapterItem = ({ light, item, history }) => {
  return (
    <div
      className="chapter-item"
      style={{ background: light ? "#fafafa" : "#121212" }}
      onClick={() => history.push(`/detail/pages/${item.id}`)}
    >
      <div className="chapter-info">
        <h6>{item.title} </h6>
        <span>{item.created_date}</span>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    light: state.bookReducer.light,
  };
};

export default connect(mapStateToProps)(withRouter(ChapterItem));
