import React, { useEffect } from "react";
import Slider from "react-slick";
import BookItem from "./book-item";
import { connect } from "react-redux";
import { getMostRatedBooks } from "../../service/thunk/books-thunk";

let settings;

if (window.screen.width > 850) {
  settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 1,
  };
} else if (window.screen.width > 600) {
  settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
  };
} else {
  settings = {
    dots: false,
    infinite: false,
    arrows: false,
    speed: 500,
    slidesToShow: 2,
    slidesToScroll: 1,
  };
}

const BookList = ({ books, getMostRatedBooks }) => {
  useEffect(() => {
    getMostRatedBooks();
    console.log("getMostRatedBooks");
  }, [getMostRatedBooks]);
  return (
    <div className="books-block">
      <h4 style={{ color: "white" }}>Что почитаем?</h4>
      <div className="book-slider">
        <Slider {...settings}>
          {books.map((item, index) => (
            <BookItem item={item} key={index} />
          ))}
        </Slider>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    books: state.bookReducer.mostRated,
  };
};

export default connect(mapStateToProps, { getMostRatedBooks })(BookList);
