const initialState = {
  title: "",
  description: "",
  views: 0,
  created_date: 0,
  status: "",
  category: [],
  genre: [],
  types: "",
  poster: "",
  is_liked: false,
  average_rating: 0,
  id: 0,
  chapters: [],
  page_count: 0,
  count: 0,
  Chapterloading: false,
  bookLoading: false,
  pages_info: [],
  pages: [],
  pages_loading: false,
  reviews: [],
};

const BookDetailReducer = (state = initialState, action) => {
  switch (action.type) {
    case "FETCHING_CHAPTER":
      return { ...state, Chapterloading: true };
    case "FETCHING_BOOK":
      return { ...state, bookLoading: true };
    case "FETCHING_PAGES":
      return { ...state, pages_loading: true };
    case "GET_DETAIL_BOOK":
      return {
        ...state,
        title: action.data.title,
        description: action.data.description,
        views: action.data.views,
        created_date: action.data.created_date,
        status: action.data.status,
        category: action.data.category,
        genre: action.data.genre,
        types: action.data.types,
        poster: action.data.poster,
        is_liked: action.data.is_liked,
        average_rating: action.data.average_rating,
        id: action.data.id,
        bookLoading: false,
      };
    case "GET_CHAPTERS":
      return {
        ...state,
        page_count: action.data.page_count,
        count: action.data.count,
        chapters: action.data.results,
        Chapterloading: false,
      };
    case "GET_PAGES":
      return {
        ...state,
        pages_info: {
          title: action.data.title,
          created_date: action.data.created_date,
        },
        pages: [action.data.page],
        pages_loading: false,
      };
    case "GET_REVIEWS":
      return {
        ...state,
        reviews: action.data,
      };
    default:
      return state;
  }
};

export default BookDetailReducer;
