import React, { useEffect, useState } from "react";
import { withRouter } from "react-router-dom";
import "./style.css";

const ReviewItem = ({ item, light, history }) => {
  const [starPrecent, setPersent] = useState(0);

  useEffect(() => {
    const value = item.star;
    const persent = (parseInt(value) / 5) * 100;
    setPersent(persent);
  }, [item]);
  return (
    <div
      className="review-item"
      style={{ background: light ? "#fafafa" : "#121212" }}
    >
      <div className="review-img-wrapper">
        <img src={item.user.image} className="review-img" alt="poster" />
      </div>
      <div className="review-text">
        <h6
          onClick={() => history.push("/profile/" + item.user.id)}
          className="comment-title"
          style={{ display: "inline", cursor: "pointer" }}
        >
          {item.user.username}
        </h6>
        <div className="rating">
          <div className="rating__body">
            <div
              className="rating__active"
              style={{ width: `${starPrecent}%` }}
            ></div>
            <div className="rating__items">
              <input type="radio" className="rating__item" value={1} />
              <input type="radio" className="rating__item" value={2} />
              <input type="radio" className="rating__item" value={3} />
              <input type="radio" className="rating__item" value={4} />
              <input type="radio" className="rating__item" value={5} />
            </div>
          </div>
        </div>
        <p dangerouslySetInnerHTML={{ __html: item.text }} />
      </div>
    </div>
  );
};

export default withRouter(ReviewItem);
