import React, { useRef, useState } from "react";
import Profile from "../../service/api/profile";

const ImagePopUp = ({ isOpen, setOpen }) => {
  const inputEl = useRef(null);
  const [res, setRes] = useState([]);

  const load = async () => {
    const api = new Profile();
    const files = inputEl.current.files;
    let data = new FormData();
    data.append("image", files[0], files[0].name);
    api
      .setimage(data, localStorage.getItem("access").slice(1, -1))
      .then((r) => {
        setRes(["success", "Загружено успешно"]);
      })
      .catch((error) => {
        setRes(["danger", "Ошибка при загрузке"]);
      });
  };
  if (isOpen) {
    return (
      <div className="wrapper">
        <div className="popup-background">
          <div className="popup-form">
            <span className="close" onClick={() => setOpen(false)}>
              X
            </span>
            {res !== [] ? (
              <span className={`text-${res[0]} d-block`}>{res[1]}</span>
            ) : null}
            <input type="file" ref={inputEl} style={{ display: "block" }} />
            <button
              className="btn btn-warning d-block mt-2"
              onClick={() => load()}
            >
              Загрузить
            </button>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
};

export default ImagePopUp;
