import {
  GET_CHAPTERS,
  GET_DETAIL_BOOK,
  FETCHING_CHAPTER,
  FETCHING_BOOK,
  FETCHING_PAGES,
  GET_PAGES,
  GET_REVIEWS,
} from "../actions/book-action";
import BooksApi from "../api/books-api";

const booksApi = new BooksApi();

const getChapters = (id, page) => {
  return (dispatch) => {
    dispatch(FETCHING_CHAPTER());
    booksApi.getChapterByBook(id, page).then((data) => {
      dispatch(GET_CHAPTERS(data));
    });
  };
};

const getDetailBook = (id, access) => {
  return (dispatch) => {
    dispatch(FETCHING_BOOK());
    booksApi.getDetailBook(id, access).then((data) => {
      dispatch(GET_DETAIL_BOOK(data));
    });
  };
};

const getPages = (id) => {
  return (dispatch) => {
    dispatch(FETCHING_PAGES());
    booksApi.getPagesByChapter(id).then((data) => {
      dispatch(GET_PAGES(data));
    });
  };
};

const getReviews = (id) => {
  return (dispatch) => {
    dispatch(FETCHING_PAGES());
    booksApi.getReviews(id).then((data) => {
      dispatch(GET_REVIEWS(data));
    });
  };
};

export { getChapters, getDetailBook, getPages, getReviews };
