import React, { useEffect } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { getUserById } from "../../service/thunk/profile";

let Public = ({ params, light, user, getUserById }) => {
  useEffect(() => {
    getUserById(params);
  }, [getUserById, params]);

  let date_joined;
  if (user.date_joined !== undefined) {
    const date_joined_id = user.date_joined.indexOf("T");
    date_joined = user.date_joined.slice(0, date_joined_id);
  }

  return (
    <div className="wrapper">
      <div className={"profile-wrapper " + (light ? "light-bg" : "black-bg")}>
        <div className="custom-container">
          <div
            className={
              "detail-block row " +
              (light ? "detail-block-lt" : "detail-block-bl")
            }
          >
            <div className="detail-img-block col-12 col-sm-3">
              <div
                style={{
                  display: "flex",
                  width: "100%",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <img
                  src={user.image}
                  className="detail-img profile-img"
                  alt="profile-img"
                />
              </div>
            </div>
            <div
              className="detail-info-block col-12 col-sm-9"
              style={light ? { color: "black" } : { color: "white" }}
            >
              <div className="detail-nav">
                <span className={"nav-btn active-btn"}>Информация</span>
              </div>
              <div style={{ marginLeft: "10px" }}>
                <div>
                  <h6>
                    Имя: <span>{user.first_name}</span>
                  </h6>
                </div>
                <div>
                  <h6>
                    Никнейм: <span>{user.username}</span>
                  </h6>
                </div>
                <div>
                  <h6>
                    Дата создания: <span>{date_joined}</span>
                  </h6>
                </div>
                <div>
                  <h6>
                    Пол:{" "}
                    <span>{user.gender ? user.gender : "Не определен"}</span>
                  </h6>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    light: state.bookReducer.light,
    user: state.ProfileReducer.user,
  };
};

export default connect(mapStateToProps, { getUserById })(withRouter(Public));
