import React, { useEffect } from "react";
import { connect } from "react-redux";
import { getPages } from "../../service/thunk/detail-thunk";

const PagesBlock = ({ params, pages, getPages, info, pages_loading }) => {
  useEffect(() => {
    getPages(params.id);
  }, [params, getPages]);

  return (
    <div className="pages-wrapper">
      <div className="custom-container">
        <div style={{ display: "grid", justifyContent: "center" }}>
          {pages_loading ? (
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "center",
                marginTop: "5vh",
              }}
            >
              <div className="spinner-border" role="status">
                <span className="sr-only"></span>
              </div>
            </div>
          ) : (
            <div>
              <h3 style={{ color: "white" }}>{info.title}</h3>
              <span>Дата выхода: {info.created_date}</span>
              <div style={{ display: "grid" }}>
                {pages.length > 0
                  ? pages[0].map((item, index) => (
                      <img
                        src={item.picture}
                        key={index}
                        style={{ maxWidth: "100%" }}
                        alt="page"
                      />
                    ))
                  : null}
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    pages: state.BookDetailReducer.pages,
    info: state.BookDetailReducer.pages_info,
    pages_loading: state.BookDetailReducer.pages_loading,
  };
};

export default connect(mapStateToProps, { getPages })(PagesBlock);
