import React from "react";
import "./style.css";
import Search from "../catalog/search";
import BookList from "../catalog/book-list";
import Catalog2 from "../catalog-2/main-block";
import { CHANGE_THEME } from "../../service/actions/books-action";
import { connect } from "react-redux";

const MainPage = ({ CHANGE_THEME }) => {
  return (
    <div className="wrapper">
      <div className="bg-im">
        <div className="custom-container">
          <Search />
          <BookList />
        </div>
      </div>
      <Catalog2 />
    </div>
  );
};
export default connect(null, { CHANGE_THEME })(MainPage);
