import {
  FETCHING_DATA,
  GET_THE_MOST_RATED,
  GET_THE_MOST_POPULAR,
  GET_THE_NEWEST,
  GET_TYPES,
  GET_CATEGORY,
  GET_GENRE,
  GET_BOOKS,
} from "../actions/books-action";
import BooksApi from "../api/books-api";

const booksApi = new BooksApi();

const getMostRatedBooks = () => {
  return (dispatch) => {
    dispatch(FETCHING_DATA());
    booksApi.getMostRatedBooks().then((data) => {
      dispatch(GET_THE_MOST_RATED(data));
    });
  };
};

const getMostPopularBooks = () => {
  return (dispatch) => {
    dispatch(FETCHING_DATA());
    booksApi.getMostPopularBooks().then((data) => {
      dispatch(GET_THE_MOST_POPULAR(data));
    });
  };
};

const getTheNewestChapters = () => {
  return (dispatch) => {
    dispatch(FETCHING_DATA());
    booksApi.getTheNewestChapters().then((data) => {
      dispatch(GET_THE_NEWEST(data));
    });
  };
};

const getTypes = () => {
  return (dispatch) => {
    dispatch(FETCHING_DATA());
    booksApi.getFilterList("types").then((data) => {
      dispatch(GET_TYPES(data));
    });
  };
};

const getCategory = () => {
  return (dispatch) => {
    dispatch(FETCHING_DATA());
    booksApi.getFilterList("category").then((data) => {
      dispatch(GET_CATEGORY(data));
    });
  };
};
const getGenre = () => {
  return (dispatch) => {
    dispatch(FETCHING_DATA());
    booksApi.getFilterList("genre").then((data) => {
      dispatch(GET_GENRE(data));
    });
  };
};

const getBooks = (filter) => {
  return (dispatch) => {
    dispatch(FETCHING_DATA());
    booksApi.getAllOrFilterBooks(filter).then((data) => {
      dispatch(GET_BOOKS(data));
    });
  };
};
export {
  getMostRatedBooks,
  getMostPopularBooks,
  getTheNewestChapters,
  getGenre,
  getTypes,
  getCategory,
  getBooks,
};
