import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import {
  getDetailBook,
  getChapters,
  getReviews,
} from "../../service/thunk/detail-thunk";
import { GET_DETAIL_BOOK } from "../../service/actions/book-action";
import RatingPopUp from "./rating-popup";
import BooksApi from "../../service/api/books-api";
import ChapterItem from "./chapter-item";
import ReviewItem from "./review-item";
import Pagination from "./pagination";
import "./style.css";

const DetailPage = ({
  light,
  params,
  history,
  getDetailBook,
  getChapters,
  title,
  description,
  views,
  created_date,
  status,
  category,
  genre,
  types,
  poster,
  is_liked,
  average_rating,
  chapters,
  page_count,
  chapterloading,
  bookLoading,
  isAuth,
  id,
  GET_DETAIL_BOOK,
  getReviews,
  reviews,
  myuser,
}) => {
  const [subUrl, setSub] = useState(1);
  const [bookId, setBookId] = useState(0);
  const [open, setOpen] = useState(false);

  useEffect(() => {
    const access = JSON.parse(localStorage.getItem("access"));
    const match = params.id.match(/id=(\d{0,})/gm);
    const id = match[0].replace("id=", "");
    getDetailBook(id, access);
    getChapters(id);
    getReviews(id);
    setBookId(id);
  }, [getDetailBook, getChapters, getReviews]); // getting book data

  useEffect(() => {
    const page = params.id.match(/&page=(\d{0,})/gm);
    if (page) {
      const num = page[0].replace("&page=", "");
      getChapters(bookId, num);
    }
  }, [params, bookId, getChapters]); //set pagination settings

  const addLike = () => {
    if (isAuth || JSON.parse(localStorage.getItem("isAuth"))) {
      const api = new BooksApi();
      const access = localStorage.getItem("access").slice(1, -1);
      api.setLike(id, access).then((data) => {
        GET_DETAIL_BOOK(data);
      });
    } else {
      history.push("/login/");
    }
  };

  return (
    <React.Fragment>
      <div className={"detail-wrapper " + (light ? "light-bg" : "black-bg")}>
        <svg style={{ display: "none" }}>
          <symbol id="star" viewBox="0 -10 511.98685 511">
            <g>
              <path
                d="m510.652344 185.902344c-3.351563-10.367188-12.546875-17.730469-23.425782-18.710938l-147.773437-13.417968-58.433594-136.769532c-4.308593-10.023437-14.121093-16.511718-25.023437-16.511718s-20.714844 6.488281-25.023438 16.535156l-58.433594 136.746094-147.796874 13.417968c-10.859376 1.003906-20.03125 8.34375-23.402344 18.710938-3.371094 10.367187-.257813 21.738281 7.957031 28.90625l111.699219 97.960937-32.9375 145.089844c-2.410156 10.667969 1.730468 21.695313 10.582031 28.09375 4.757813 3.4375 10.324219 5.1875 15.9375 5.1875 4.839844 0 9.640625-1.304687 13.949219-3.882813l127.46875-76.183593 127.421875 76.183593c9.324219 5.609376 21.078125 5.097657 29.910156-1.304687 8.855469-6.417969 12.992187-17.449219 10.582031-28.09375l-32.9375-145.089844 111.699219-97.941406c8.214844-7.1875 11.351563-18.539063 7.980469-28.925781zm0 0"
                fill="#ffc107"
              />
            </g>
          </symbol>
          <symbol id="heart" viewBox="0 0 512 512">
            <g>
              <path d="m246.122 477.289c-144.417-126.367-246.122-193.304-246.122-299.774 0-80.513 57.4-146.515 136-146.515 54.544 0 95.017 33.497 120 81.015 24.981-47.515 65.454-81.015 120-81.015 78.609 0 136 66.015 136 146.515 0 106.457-101.572 173.291-246.122 299.773-5.657 4.949-14.1 4.949-19.756.001z" />
            </g>
          </symbol>
          <symbol id="eye" viewBox="0 0 511.999 511.999">
            <g>
              <g>
                <g>
                  <path
                    d="M508.745,246.041c-4.574-6.257-113.557-153.206-252.748-153.206S7.818,239.784,3.249,246.035
			c-4.332,5.936-4.332,13.987,0,19.923c4.569,6.257,113.557,153.206,252.748,153.206s248.174-146.95,252.748-153.201
			C513.083,260.028,513.083,251.971,508.745,246.041z M255.997,385.406c-102.529,0-191.33-97.533-217.617-129.418
			c26.253-31.913,114.868-129.395,217.617-129.395c102.524,0,191.319,97.516,217.617,129.418
			C447.361,287.923,358.746,385.406,255.997,385.406z"
                  />
                </g>
              </g>
              <g>
                <g>
                  <path
                    d="M255.997,154.725c-55.842,0-101.275,45.433-101.275,101.275s45.433,101.275,101.275,101.275
			s101.275-45.433,101.275-101.275S311.839,154.725,255.997,154.725z M255.997,323.516c-37.23,0-67.516-30.287-67.516-67.516
			s30.287-67.516,67.516-67.516s67.516,30.287,67.516,67.516S293.227,323.516,255.997,323.516z"
                  />
                </g>
              </g>
            </g>
          </symbol>
        </svg>
        <div className="detail-container">
          {bookLoading ? (
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "center",
                marginTop: "5vh",
              }}
            >
              <div className="spinner-border" role="status">
                <span className="sr-only"></span>
              </div>
            </div>
          ) : (
            <div
              className={
                "detail-block row " +
                (light ? "detail-block-lt" : "detail-block-bl")
              }
            >
              <div className="detail-img-block col-12 col-sm-3">
                <div
                  style={{
                    display: "flex",
                    width: "100%",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                >
                  <img src={poster} className="detail-img" alt="poster" />
                </div>
                <div className="read">
                  <button
                    className="btn btn-warning btn-block"
                    onClick={() => setSub(2)}
                  >
                    Читать
                  </button>
                </div>
              </div>

              <div
                className="detail-info-block col-12 col-sm-9"
                style={light ? { color: "black" } : { color: "white" }}
              >
                <div className="detail-header">
                  <h2 style={{ overflowX: "auto", overflowY: "hidden" }}>
                    {title} ({status})
                  </h2>
                  <div className="header-display">
                    <div
                      style={{
                        marginRight: "5%",
                        display: "flex",
                        alignItems: "center",
                      }}
                    >
                      <svg
                        className="icon-detail"
                        onClick={() => setOpen(true)}
                      >
                        <use xlinkHref="#star"></use>
                      </svg>
                      <span className="star-value">{average_rating}</span>
                    </div>
                    <div
                      style={{
                        marginRight: "5%",
                        display: "flex",
                        alignItems: "center",
                      }}
                    >
                      <svg
                        className={
                          "icon-detail heart" + (is_liked ? " liked" : "")
                        }
                        onClick={() => addLike()}
                      >
                        <use xlinkHref="#heart"></use>
                      </svg>
                    </div>
                    <div
                      style={{
                        marginRight: "5%",
                        display: "flex",
                        alignItems: "center",
                      }}
                    >
                      <svg className="icon-detail eye">
                        <use xlinkHref="#eye"></use>
                      </svg>
                      <span className="star-value">{views}</span>
                    </div>
                    <div
                      style={{
                        marginRight: "5%",
                        display: "flex",
                        alignItems: "center",
                        fontSize: "18px",
                      }}
                    >
                      <span className="star-value">{types}</span>
                    </div>
                    <div
                      style={{
                        marginRight: "5%",
                        display: "flex",
                        fontSize: "18px",
                        alignItems: "center",
                      }}
                    >
                      <span className="star-value">{created_date}</span>
                    </div>
                  </div>
                </div>
                <div className="detail-main">
                  <div className="detail-nav">
                    <span
                      className={
                        "nav-btn " + (subUrl === 1 ? "active-btn" : "")
                      }
                      onClick={() => {
                        setSub(1);
                      }}
                    >
                      Описание
                    </span>
                    <span
                      className={
                        "nav-btn " + (subUrl === 2 ? "active-btn" : "")
                      }
                      onClick={() => {
                        setSub(2);
                      }}
                    >
                      Главы
                    </span>
                    <span
                      className={
                        "nav-btn " + (subUrl === 3 ? "active-btn" : "")
                      }
                      onClick={() => {
                        setSub(3);
                      }}
                    >
                      Отзывы
                    </span>
                  </div>

                  {subUrl === 1 && (
                    <div className="detail-description">
                      <div className="detail-genres">
                        <span
                          style={{
                            display: "block",
                            overflowX: "scroll",
                          }}
                        >
                          Категория:{" "}
                          {category.map((item) => (
                            <span>{item}, </span>
                          ))}
                        </span>
                        <span
                          style={{
                            display: "block",
                          }}
                        >
                          Жанр:{" "}
                          {genre.map((item) => (
                            <span>{item}, </span>
                          ))}
                        </span>
                      </div>
                      <h6>Краткое описание: </h6>
                      <div className="description">{description}</div>
                    </div>
                  )}
                  {subUrl === 2 && (
                    <div className="chapter-block">
                      {chapterloading ? (
                        <div
                          style={{
                            width: "100%",
                            display: "flex",
                            justifyContent: "center",
                            marginTop: "5vh",
                          }}
                        >
                          <div className="spinner-border" role="status">
                            <span className="sr-only"></span>
                          </div>
                        </div>
                      ) : (
                        chapters.map((item) => (
                          <React.Fragment>
                            <ChapterItem item={item} />
                          </React.Fragment>
                        ))
                      )}
                      <Pagination
                        light={light}
                        params={params.id}
                        numPage={page_count}
                      />
                    </div>
                  )}
                  {subUrl === 3 && (
                    <div
                      style={{
                        marginLeft: "5px",
                        paddingTop: "10px",
                      }}
                    >
                      {reviews
                        ? reviews.map((item) => (
                            <ReviewItem item={item} light={light} />
                          ))
                        : null}
                    </div>
                  )}
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
      <RatingPopUp
        isOpen={open}
        setOpen={setOpen}
        isAuth={isAuth}
        book_id={bookId}
        user_id={myuser.id}
      />
    </React.Fragment>
  );
};
const mapStateToProps = (state) => {
  return {
    light: state.bookReducer.light,
    title: state.BookDetailReducer.title,
    description: state.BookDetailReducer.description,
    views: state.BookDetailReducer.views,
    created_date: state.BookDetailReducer.created_date,
    status: state.BookDetailReducer.status,
    category: state.BookDetailReducer.category,
    genre: state.BookDetailReducer.genre,
    types: state.BookDetailReducer.types,
    poster: state.BookDetailReducer.poster,
    is_liked: state.BookDetailReducer.is_liked,
    average_rating: state.BookDetailReducer.average_rating,
    chapters: state.BookDetailReducer.chapters,
    page_count: state.BookDetailReducer.page_count,
    chapterloading: state.BookDetailReducer.Chapterloading,
    bookLoading: state.BookDetailReducer.bookLoading,
    reviews: state.BookDetailReducer.reviews,
    id: state.BookDetailReducer.id,
    isAuth: state.ProfileReducer.isAuth,
    myuser: state.ProfileReducer.myuser,
  };
};

export default connect(mapStateToProps, {
  getDetailBook,
  getChapters,
  GET_DETAIL_BOOK,
  getReviews,
})(withRouter(DetailPage));
